import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { setupWebSockets } from 'src/app/shared/webSocket';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {

  currentFluxo: number;

  constructor(
    private route: Router,
    private storage: StorageService,
  ) { }

  ngOnInit() {
    setupWebSockets();
  }

  setFluxo(fluxo: number) {
    this.currentFluxo = fluxo;
    this.route.navigateByUrl(`fluxo-${fluxo}`);
  }

  canLoad(fluxo: number): boolean {
    const { perfil } = this.storage.getLocalUser().user;

    switch (perfil.nome) {
      case 'OPERADOR':
        return [2, 3, 4, 5, 7, 9].includes(fluxo);

      case 'SUPERVISOR':
        return [1, 6, 8, 10, 11].includes(fluxo);

      case 'GERENTE':
        return [1, 6, 8, 10, 11].includes(fluxo);

      case 'ADMIN':
        return [-1].includes(fluxo);
    }


    return false;
  }

}